    export default {
        data() {
            return {
                show: false,  // 判断筛选弹窗显示或隐藏
                //  筛选框选择
                shopName: ['未处理','已联系','已解决','跟进中','无法联系','未达成','其他','所有'],
                company_name: "",  // 类型
                suggestList: [],
                typelist: ['未处理','已联系','已解决','跟进中','无法联系','未达成','其他'],
                numId: '',   // 筛选类型
                page: 1,            //  呼叫列表请求页码页数
                maxPage: 1,
                limit: 10,
                loadText: '下拉加载更多',        //  呼叫中列表  下拉提示词
                loading: true,                  //   呼叫中列表   下拉圈圈
                onFetching: false,
            }
        },
        mounted(){
            this.getInfo();
        },
        methods:{
            getInfo(){
                if (this.onFetching) {
                    // do nothing
                } else {
                    this.onFetching = true;
                    if(this.page > this.maxPage){
                        this.onFetching = false;
                        return;
                    }
                    this.common.commonFun.ajax({
                        dom: this,
                        url: this.common.apiUrl.suggest,
                        type: "post",
                        data: {
                            page: this.page,
                            limit: this.limit,
                            suggest_status: this.numId,
                        },
                        success: (response, allResponse) => {
                            // console.log('response',response)
                            this.onFetching = true;
                            this.maxPage = Math.ceil(allResponse.count/this.limit) || 1;
                            if(this.page >= this.maxPage){
                                this.onFetching = false;
                                console.log('滚动结束');
                                this.loadText = "已经到底了哦~";
                                this.loading = false;
                            }
                            this.suggestList = this.suggestList.concat(response);
                            this.onFetching = false;
                            this.page ++;
                        }
                    });
                }
            },
//            点击筛选触发
            navRigth(){
                console.log("我要筛选了")
                this.show = true;
            },
//            筛选弹窗确定按钮
            choice(){
                this.show = false;
                this.shopName.forEach((item,index)=>{
                    if(this.company_name == item){
                        this.numId = index
                    }
                });
                if(this.company_name == '所有'){
                    this.numId = ''
                };
                this.suggestList = [];
                this.$refs.scrollerBottom.reset({top: 0});
                this.page = 1;
                this.maxPage = 1;
                this.loadText = '下拉加载更多';
                this.loading = true;
                this.getInfo()
            },
//            查看详情
            particulars(item){
                this.$router.push({
                    name: 'suggestDetails',
                    params:{
                        suggest: item.suggest,
                        id: item.id,
                        time: item.create_time,
                    }
                })
            }
        }
    }



